.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	&:focus {
		box-shadow: none;
	}
}

.btn-border {
	color: $gray;
	border: 0.75px solid $gray;
	width: 130px;
	height: 30px;
	@include radius(0);
	padding: 0;
	font-weight: 100;
}

.btn-red {
	height: 75px;
	width: 350px;
	font-weight: 500;
	font-size: 24px;
	line-height: 29px;
	color: $text-color-white;
	background-color: $btn-red;
	box-shadow: 0px 10px 35px $shadow-red;
	@include radius(0);
}

.social-networks {
	display: flex;
	> li {
		&:first-child {
			margin-right: 17px;
		}
		> a {
			display: flex;
			width: 25px;
		}
	}
}

.title-section {
	@include font($font-size-h1, $line-height-h1, $title-color, 900);
}

.title-white {
	@include font($font-size-h1, $line-height-h1, $title-color-wite, 900);
}

.title-text-border {
	color: $text-color-white;
	font-weight: 900;
	font-size: 144px;
	line-height: 173px;
	text-shadow: -1px -1px 0 $shadow-light-text, 1px -1px 0 $shadow-light-text, -1px 1px 0 $shadow-light-text,
		1px 1px 0 $shadow-light-text;
	text-align: center;
	margin-bottom: -38px;
	position: relative;
	z-index: -1;
}

.input-bark {
	height: 55px;
	width: 100%;
	background-color: $bg-color-input;
	border: none;
	padding: 0 21px;
	@include radius(0);
	box-shadow: none;
	color: $text-color-white;
	&::placeholder {
		color: $white;
		opacity: 0.5;
		font-weight: 100;
	}
}

.cards-dark {
	background-color: $bg-dark;
	color: $text-color-white;
	box-shadow: 0px 70px 100px $shadow-dark;
	padding: 37px 95px 97px;
}

// style input type="number"
.spinner {
	width: 255px;
	position: relative;
	text-align: center;
	input[type="number"] {
		-moz-appearance: textfield;
		&::-webkit-inner-spin-button,
		&::-webkit-outer-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}
	input {
		width: 100%;
		height: 55px;
		background: $bg-spinner;
		border: none;
		text-align: center;
		font-size: 18px;
		line-height: 22px;
		font-weight: 300;
	}
	&__nav {
		position: relative;
	}
	&__btn {
		height: 55px;
		width: 55px;
		cursor: pointer;
		font-size: 24px;
		line-height: 29px;
		color: $text-color-white;
		background-color: $btn-red;
		display: flex;
		align-items: center;
		justify-content: center;
		user-select: none;
		&.spinner-up,
		&.spinner-down {
			bottom: 0;
			position: absolute;
		}
		&.spinner-up {
			right: 0;
		}
		&.spinner-down {
			left: 0;
		}
	}
}
